import React, { Component } from 'react';
import logo from './logo.svg';
import { Navbar, Jumbotron, Button } from 'react-bootstrap';
//import './App.css';

class App extends Component {
  state ='';

  componentDidMount() {
    this.callApi()
      .then(res => this.setState(res))
      .catch(err => console.log(err));
  }

  callApi = async () => {
    const response = await fetch('/api/BlockchainClis/status');

    var body = await response.json();

    if (response.status !== 200) throw Error(body.message);

    return body.status;
  };

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
        {<div className="App-intro">
          <FruitList orgs={this.state} />
        </div>}
      </div>
    );
  }
}

class FruitList extends React.Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <div className="container">
        <ul className="list-group text-center">
          { this.props && this.props.orgs &&
            
           //console.log(this.props.orgs['orgs']) &&

            Object.entries(this.props.orgs['orgs']).map((index,value) => {
              var org=this.props.orgs['orgs']
              
              var indexi=index+1
              console.log(index,index[1][value+1])
              return (<li className="list-group-item list-group-item-info" key={index[0]} >{index[1][value+1]}</li>
            )})
          }
        </ul>
      </div>
     );
   }
 };

export default App;



